.burger {
  position: relative;
  width: 40px;
  height: 21px;
  border: none;
  background-color: transparent;
  padding: 0;
}
.burger span {
  display: block;
  position: absolute;
  background-color: #282626;
  border-radius: 2px;
  position: absolute;
  left: 0;
  top: calc(50% - 1.5px);
  width: 100%;
  height: 3px;
  transition: all 0.3s ease 0s;
}
.burger:before, .burger:after {
  content: "";
  background-color: #282626;
  border-radius: 2px;
  position: absolute;
  height: 3px;
  left: 0;
  transition: all 0.3s ease 0s;
}
.burger:before {
  width: 100%;
  top: 0;
}
.burger:after {
  width: 29px;
  bottom: 0;
}
.burger:hover.burger:before, .burger:hover.burger:after, .burger:hover span {
  background-color: #5B5656;
}
.burger:active.burger:before, .burger:active.burger:after, .burger:active span {
  background-color: #5B5656;
}
.burger.active span {
  transform: scale(0);
}
.burger.active:before {
  transform: rotate(45deg);
  width: 25px;
  top: 8px;
}
.burger.active:after {
  transform: rotate(-45deg);
  width: 25px;
  bottom: 10px;
  left: 0;
}

